body {
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    line-height: 1.7;
    font-size: var(--default-font-size);
    color: var(--color-brown);
}

.paragraph{
    &:not(:last-child){
        margin-bottom: var(--horizontal-gutter);
    }
}

.heading-3 {
    font-family: 'Roboto', sans-serif;
    text-transform: uppercase;
    font-weight: 400;
    letter-spacing: 3px;
    font-size: 4rem;
}
